
.homePageSect{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

p,h1{
    font-family: Lato;
}

button {
	-webkit-box-shadow: 0px 1px 0px 0px #fff6af;
	        box-shadow: 0px 1px 0px 0px #fff6af;
	background-color:#fcb35f;
	border-radius:2px;
	border:1px solid #ffaa22;
	display:inline-block;
	cursor:pointer;
	color:#333333;
	font-size:15px;
	font-weight:bold;
	padding:10px 100px;
	text-decoration:none;
	margin-bottom: 100px;
}

button:hover {
	background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #ffab23), to(#bbb270));
	background:-webkit-linear-gradient(top, #ffab23 5%, #bbb270 100%);
	background:-o-linear-gradient(top, #ffab23 5%, #bbb270 100%);
	background:linear-gradient(to bottom, #ffab23 5%, #bbb270 100%);
	background-color:#ffab23;
}
button:active {
	position:relative;
	top:1px;
}

.appTitle{
	font-size: 23px;
}
.homepageDetails{
	margin-bottom: 20px;
	font-size: 16px;
}

@media (min-width: 600px) {
	.appTitle{
		font-size: 40px;
	}

	.homepageDetails{
		width: 75vh;
		margin-bottom: 30px;
		margin-top: 30px;
	}
  }
        



