.filter{
	display: inline;
	margin-left: 0.5em;
}

.filter,.radius{
    font-size: 14px; 
}

.filterOpen{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

label.filter{
    padding: 20px 0;
}
.radius {
    margin-right: 10px;
}
i {
    cursor: pointer;
}

i:hover{
    -webkit-transform: scale(1.6);
        -ms-transform: scale(1.6);
            transform: scale(1.6);
    color: #ffab23;
}

input#filterByHours{
    width: 12px;
    height: 12px;

}

input.submit{
    font-weight: bold;
}


